import React from "react";
import { connect } from "react-redux";
import qrLogo from "./qr-code.svg";
import list from "./list.svg";
import create from "./create.svg";
import FatCircleCta from "../../components/FatCircleCta";
import { navigateToPageAction } from "../../store/store";

const Home = ({ navigateToPage }) => (
  <>
    <FatCircleCta
      src={create}
      alt="logo"
      onClick={e => navigateToPage("/createtask")}
    />
    <FatCircleCta
      src={list}
      alt="logo"
      onClick={e => navigateToPage("/tasklist")}
    />
    <FatCircleCta
      src={qrLogo}
      alt="logo"
      onClick={e => navigateToPage("/qrread")}
    />
  </>
);

const mapDispatchToProps = dispatch => ({
  navigateToPage: page => dispatch(navigateToPageAction(page))
});

export default connect(
  null,
  mapDispatchToProps
)(Home);
