import React from "react";
import { connect } from "react-redux";
import CreateTask from "./tasks/create/CreateTask";
import TaskList from "./tasks/list/TaskList";
import QrRead from "./qr/read/QrRead";
import Home from "./home/Home";

const Nav = ({ page }) => {
  switch (page) {
    case "/tasklist":
      return <TaskList />;
    case "/createtask":
      return <CreateTask />;
    case "/qrread":
      return <QrRead />;
    case "/":
    case "/home":
    default:
      return <Home />;
  }
};

const mapStateToProps = state => ({ page: state.page });

export default connect(mapStateToProps)(Nav);
